      totalStart: 5, // 最多星星数量
      curShop: 0, // 商品星星总数量
      flagShop: false, // 商品鼠标点击后变成true，鼠标进入进出事件就不会改变背景图
      curLogistics: 0, // 物流星星总数量
      flagLogistics: false, // 物流鼠标点击后变成true，鼠标进入进出事件就不会改变背景图Attitude
      curAttitude: 0, // 态度星星总数量
      flagAttitude: false, // 态度鼠标点击后变成true，鼠标进入进出事件就不会改变背景图Attitude
      impression: [
        {
          name: '质量很好',
          id: '11',
          check: false
        }
      ], // 存放印象选项
      selectImpression: [], // 买家选择的印象
      star: 5, // 简单评论评分
      cOrderSn: 'WD195928794602', // 网单号
    }
  },
  methods: {
    /**
      * @description  切换选中评级
    */
    toggleSelect(i) {
      if (this.star === i) {
        this.star = null
      } else {
        this.star = i
      }
    },
    /**
      * @description  评价程度
    */
    radioChange: function(e) {
      this.star = e
    },
    /**
     * @description  商品评论星级选中
    */
    selectedShop(n) {
      this.flagShop = true;
      this.curShop = n;
    },
    /**
     * @description  物流评论星级选中
    */
    selectedLogistics(n) {
      this.flagLogistics = true;
      this.curLogistics = n;
    },
    /**
     * @description  态度评论星级选中
    */
    selectedAttitude(n) {
      this.curAttitude = n;
    },
    /**
     * @description  买家印象选择
    */
    impressArr(item, index) {
      item.check = !item.check
      if (item.check) {
        this.selectImpression.push(item.impressionsProdTypeId)
      } else {
        // 删除改项
        this.selectImpression.forEach((item, index) => {
          const i = this.selectImpression.findIndex(value => {
            if (item === value) {
              return value
            }
          })
          if (i !== -1) {
            this.selectImpression.splice(i, 1)
          }
        })
      }
      this.selectImpression = [...new Set(this.selectImpression)]
    },
    /**
     * @description  提交
    */
    submission() {
      this.$toast.loading({
        forbidClick: true,
        message: '加载中...',
        duration: 0
      })
      for (const value in this.selectImpression) {
        if (this.selectImpression[value].check === false) {
          this.selectImpression.splice(value, 1)
        }
      }
      var context = this
      if (!context.content.trim()) {
        context.$toast.fail('请输入评价内容！')
        return
      }
      if (context.shop.isMyOrder) {
        if (!context.curShop) {
          context.$toast.fail('评价信息不完整！')
          return
        }
      } else {
        if (!context.star) {
          context.$toast.fail('评价信息不完整！')
          return
        }
      }
      // picUrl图片路径处理
      if (context.images.length) {
        context.loading = true
        personalApi.uploadPictureBybase64({
          base64s: context.images.map(img => img.content.split(',')[1])
        }).then(imgRes => {
          context.loading = false
          if (imgRes.data.success) {
            const data = {
              cOrderSn: context.cOrderSn, // 网单号
              commentContent: context.content, // 评价内容
              fileIds: imgRes.data.data.fileList.map(img => img.imgUrl).join(), // 评论图片地址
              impressions: context.selectImpression.join(), // 印象标签id
              productStar: context.curShop, // 商品评分
              serviceStar: context.curAttitude, // 服务评分
              shippingStar: context.curLogistics, // 物流评分
              star: context.star // 总评分(好中差评) 好评:5 中评:3 差评:1
            }
            // 参数去空串
            context.exceptEmpty(data)
            // 评价提交
            personalApi.submitComment(data).then(res => {
              if (res.data.success) {
                context.$toast.clear()
                context.$router.replace({ path: '/success', query: { cOrderSn: this.cOrderSn } })
              } else {
                context.$toast.fail(res.data.message)
              }
            })
          } else {
            context.$toast(imgRes.data.message)
          }
        }).catch(() => {
          this.loading = false
          context.$toast.fail('上传图片失败')
        })
      } else {
        const data = {
          cOrderSn: context.cOrderSn, // 网单号
          commentContent: context.content, // 评价内容
          impressions: context.selectImpression.join(), // 印象标签id
          productStar: context.curShop, // 商品评分
          serviceStar: context.curAttitude, // 服务评分
          shippingStar: context.curLogistics, // 物流评分
          star: context.star // 总评分(好中差评) 好评:5 中评:3 差评:1
        }
        // 参数去空串
        context.exceptEmpty(data)

        // 评价提交
        personalApi.submitComment(data).then(res => {
          if (res.data.success) {
            this.$toast.clear()
            this.$router.replace({ path: '/success', query: { cOrderSn: this.cOrderSn } })
          } else {
            this.$toast.fail(res.data.message)
          }
        })
      }
    },
    /**
     * @description  评价页详情
    */
    details(order) {
      // 参数
      const data = {
        cOrderSn: order // 网单号
      }
      personalApi.commentPage(data).then(res => {
        if (res.data.success) {
          this.shop = res.data.data

          // this.shop.impression遍历加check属性，判断当前的状态是否选中
          this.shop.impression.forEach(item => {
            this.$set(item, 'check', false)
          });
        } else {
          this.$toast.fail(res.data.message)
        }
      })
    },
    /**
     * @description  参数去空串
    */
    exceptEmpty(obj) {
      for (const key in obj) {
        if (key !== 'commentContent') {
          // 去除对象内多余的空值key
          if (obj[key] === '' || obj[key] === 0) {
            delete obj[key]
          }
        }
      }
    },

    /**
